@charset "utf-8";

@import url(reset.css);

/* *****************   ****************** */

body {
  position: relative;
  overflow-x: hidden;
}

/* Title Area Start */
.title{width: 100%; height: 63px; margin-bottom: 84px;}
.title h2{
  width: 100%; height: 63px; text-align: center;
  font: 400 2.4rem/1.5 'Nanum Gothic', sans-serif;
  position: relative;
}
.title h2::after{
  content: ''; display: block; width: 100px; height: 3px;
  background-color: #3483de;
  position: absolute; left: 50%; bottom: 0; margin-left: -50px;
}

/* Button Area Start */
.button-default{
  display: inline-block;
  width: 200px; height: 63px;
  border: 1px solid #fff;
  text-align: center;
  font: 700 1.4rem/63px 'Nanum Gothic', sans-serif;
  position: relative; text-decoration: none;
}
.button-default:hover{ text-decoration: none; }
.button-default::after{
  content: ''; display: block; width: 1px; height: 61px;
  position: absolute; left: 0; top: 0;

  -webkit-transition: all ease 0.4s;
  -moz-transition: all ease 0.4s;
  -o-transition: all ease 0.4s;
  transition: all ease 0.4s;
}
.button-default:hover::after{width: 100%;}

.button-default span{position: relative; z-index: 10;}

.btn1{ color: #fff; border: 1px solid #fff;}
.btn1:hover{color: #2d2d2d;}
.btn1:hover::after{background-color: #fff;}
.btn2{ color: #3483de; border: 1px solid #3483de;}
.btn2:hover{color: #fff; }
.btn2:hover::after{background-color: #3483de;}

/* Jarallax Area Start */
.jarallax {
  position: relative;
  z-index: 0;
}
.jarallax > .jarallax-img {
  position: absolute;
  object-fit: cover;
  font-family: 'object-fit: cover;';
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

/* bxSlider Reset */
.bx-wrapper{
  -webkit-box-shadow: 0 0 0 transparent;
  box-shadow: 0 0 0 transparent;
  border: 0;
  background-color: #f2f2f2;
}

/* Mobile Hidden Area Start */
#intro .container,
#banner1 .container, 
#analysis .container, 
#video .container{display: none;}

/* Container Area Reset */
#wrap>div{width: 100%;}
.container, .container-fluid{height: 100%;}
.container{padding: 105px 0;}




/* *****************   ****************** */
/* ********** Navi Area Start ********* */
#navi{
  background-color: rgba(255, 255, 255, 0); border: 0;

  -webkit-transition: all ease 0.8s;
  -moz-transition: all ease 0.8s;
  -o-transition: all ease 0.8s;
  transition: all ease 0.8s;
}
#navi .container{ padding: 0; }
#navi .container h1 a, #navi .container #gnb li a{color: #fff;}
#navi .container h1 a{
  font: 700 2rem 'Nanum Gothic', sans-serif;
}
#navi .container #gnb li a:hover{color: #c00;}


/*     */
.fixed{
  border-bottom: 1px solid #ccc !important;
  background-color: rgba(255, 255, 255, 1) !important;
}
.fixed .container h1 a, .fixed .container #gnb li a{color: #2d2d2d !important;}
.fixed .container #gnb li a:hover{color: #c00 !important;}

/* Active Class Setting */
.fixed .container #gnb li.active>a,
.fixed .container #gnb li.active>a:focus,
.fixed .container #gnb li.active>a:hover{
  color: #c00 !important;
}

/*    */
.navbar-default .navbar-nav>.active>a, 
.navbar-default .navbar-nav>.active>a:focus, 
.navbar-default .navbar-nav>.active>a:hover{
  background-color: transparent;
}





/* ********** Intro Area Start ********* */
#intro, #intro #slider, #intro #slides, #intro .item{ height: 100vh; }
#intro #slides>div{
  text-align: center;
  background: no-repeat top center;
  background-size: cover;
  color: #fff;
}
#intro #slides>div:nth-child(1){
  text-align: left;
  background-image: url(../images/lg/slider-bg1.jpg);
}
#intro #slides>div:nth-child(2){
  background-image: url(../images/lg/slider-bg2.jpg);
}
#intro #slides>div:nth-child(3){
  background-image: url(../images/lg/slider-bg3.jpg);
}

/* Slider Caption Area Start */
#intro #slides .caption{
  width: 1170px; height: auto;
  position: absolute; left: 50%; top: 50%;
  
  -webkit-transform: translate(-50%,-50%);
  -moz-transform: translate(-50%,-50%);
  -o-transform: translate(-50%,-50%);
  transform: translate(-50%,-50%);
}
#intro #slides .caption h2{
  width: 100%; height: 63px; margin-bottom: 42px;
  font: 400 6rem/1.5 'Roboto', sans-serif;
}
#intro #slides .caption p{
  width: 100%; height: 2px;
  font: 400 1.8rem/21px 'Open Sans', sans-serif;
}
#intro #slides>div:nth-child(1) .deco-text{
  width: 100%; height: 21px; margin-bottom: 21px;
  font: 500 italic 1.4rem/21px 'Open Sans', sans-serif;
}
#intro #slides>div:nth-child(1) h2{ 
  width: 600px; height: auto; margin-bottom: 63px; line-height: 1;
}

/* Controls */
#intro #slider .fa{
  position: absolute; top: 50%; margin-top: -20px; font-size: 4rem;
}




/* ********** About Area Start ********* */
#about .col-md-4{text-align: center;}
#about .col-md-4 .icon-box{
  width: 100px; height: 100px; display: inline-block;
  background-color: #3483de; border-radius: 50%; text-align: center;
  line-height: 100px; color: #fff; font-size: 30px; 
  position: relative; cursor: pointer; margin-bottom: 26px;
}
#about .col-md-4 .icon-box::after{
  content: ''; display: block;
  width: 100px; height: 100px; border: 3px solid #3483de;
  position: absolute; left: 0; top: 0;
  border-radius: 50%;

  -webkit-transition: all ease 0.4s;
  -moz-transition: all ease 0.4s;
  -o-transition: all ease 0.4s;
  transition: all ease 0.4s;
}
#about .col-md-4 .icon-box:hover::after{
  -webkit-transform: scale(1.2);
  -moz-transform: scale(1.2);
  -o-transform: scale(1.2);
  transform: scale(1.2);
}
#about .col-md-4:first-child .icon-box{font-size: 48px;}
#about .col-md-4 h3{
  width: 100%; height: 42px;
  font: 400 2rem/42px 'Nanum Gothic', sans-serif;
  margin-bottom: 21px;
}




/* ********** Work Area Start ********* */
#work{background-color: #f2f2f2;}

#work .content>div>div{position: relative;}
#work .content a{
  display: block; width: 100%; height: 100%;
}
#work .content img{width: 100%; height: 100%;}
#work .content .caption{
  width: 100%; height: 100%; position: absolute; left: 0; top: 0;
  background-color: rgba(48, 165, 255, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;

  -webkit-transition: all ease 0.8s;
  -moz-transition: all ease 0.8s;
  -o-transition: all ease 0.8s;
  transition: all ease 0.8s;
}
#work .content .caption h3{
  flex: 1; text-align: center; color: #fff;
  font: 700 1.6rem/21px 'Nanum Gothic', sans-serif;
}
#work .content a:hover .caption{opacity: 1;}

/* left Area Start */
#work .content>div:first-child>div{ width: 50%; height: 294px; float: left;}
#work .content>div:first-child>div:last-child{width: 100%;}

/* right Area Start */
#work .content>div:last-child>div{width: 50%; height: 294px; float: left;}
#work .content>div:last-child>div:first-child{height: 588px;}




/* ********** Banner1 Area Start ********* */
#banner1{height: 357px; }
#banner1 .container-fluid{
  position: relative; 
  background-image: url(../images/lg/banner-bg1.jpg);
}
#banner1 .container-fluid::after{
  content:''; display: block; width: 100%; height: 100%; 
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute; left: 0; top: 0;
}
#banner1 .container-fluid .row{
  width: 1170px; padding: 126px 0; margin: auto;
  position: relative; z-index: 10;
} 
#banner1 .container-fluid .row h2{
  width: 600px; height: 105px;
  font: 400 4.8rem/1 'Roboto', sans-serif;
  color: #fff;
}
#banner1 .container-fluid .row>div:last-child {
  padding: 21px 0 0 270px;
}




/* ********** Feature Area Start ********* */
#features .col-md-4{
  padding: 0 15px 0 70px; 
  margin-bottom: 42px;
  position: relative;
}
#features .col-md-4:nth-child(n+4){
  margin-bottom: 0;
}
#features .col-md-4 h3{
  width: 100%; height: 21px;
  font: 500 2rem/21px 'Nanum Gothic', sans-serif;
  margin-bottom: 21px;
}
#features .col-md-4 .icon-box{
  width: 40px; height: 40px; line-height: 1;
  text-align: center;
  font-size: 3.3rem; color: #3483de;
  position: absolute; left: 5px; top: 0px;
}
#features .col-md-4:nth-child(2) .icon-box{
  font-size: 3.8rem; line-height: 0.8;
}



/* ********** Analysis Area Start ********* */
#analysis .container-fluid{
  padding: 105px 0;
  background-image: url(../images/lg/banner-bg2.jpg);
  position: relative;
}
#analysis .container-fluid::after{
  content:''; display: block; width: 100%; height: 100%; 
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute; left: 0; top: 0;
}
#analysis .container-fluid .row {
  width: 1170px; height: auto; margin: auto; color: #fff;
  position: relative; z-index: 10;
}
#analysis .container-fluid .row h2{
  width: 100%; height: 42px; margin-bottom: 21px;
  font: 400 2.4rem/42px 'Nanum Gothic', sans-serif;
}
#analysis .container-fluid .row p { 
  padding-right: 30px; 
  line-height: 25px;
}

/* Circle Progress */
#analysis .circle-wrap{text-align: center; padding-top: 21px;}
#analysis .circle-wrap>div{
  width: 49%; height: 100%; display: inline-block;
  position: relative;
}
#analysis .circle-wrap>div strong{
  font: 700 1.8rem 'Nanum Gothic', sans-serif;
  position: absolute; left: 50%; top: 50%;
  
  -webkit-transform: translate(-50%,-150%);
  -moz-transform: translate(-50%,-150%);
  -o-transform: translate(-50%,-150%);
  transform: translate(-50%,-150%);
}
#analysis .circle-wrap>div h2{
  font: 700 1.8rem 'Nanum Gothic', sans-serif;
  margin-top: 21px;
}


/* ********** Plan Area Start ********* */
/* Title Area Start */
#plan .container .col-md-push-6 h2{
  width: 100%; height: 42px; margin-bottom: 21px;
  font: 400 2.4rem/42px 'Nanum Gothic', sans-serif;
}
#plan .container .col-md-push-6 p{margin-bottom: 42px;}


/* Progress Area Start */
#plan .container .col-md-pull-6{padding-right: 30px;}
#plan .container .col-md-pull-6 ul{width: 100%; height: auto;}
#plan .container .col-md-pull-6 li{width: 100%; height: 63px; margin-bottom: 21px;}
#plan .container .col-md-pull-6 li h3{
  width: 100%; height: 42px;
  font: 700 1.6rem/42px 'Nanum Gothic', sans-serif;
  position: relative;
}
#plan .container .col-md-pull-6 li h3 span{
  width: 70px; height: 42px;
  font: 700 1.6rem/42px 'Nanum Gothic', sans-serif;
  position: absolute; right: 5px; top: 0;
}
#plan .container .col-md-pull-6 li .progress, 
#plan .container .col-md-pull-6 li .progress-bar {
  height: 15px; border-radius: 30px;
}
#plan .container .col-md-pull-6 li .progress-bar {
  background-color: rgb(19, 184, 115);
}



/* ********** Video Area Start ********* */
#video .container-fluid { padding: 105px 0; color: #fff; text-align: center;}
#video .container-fluid h2{
  width: 100%; height: 42px; margin-bottom: 21px;
  font: 400 3.5rem/42px 'Nanum Gothic', sans-serif;
}
#video .container-fluid p{ margin-bottom: 42px; }


/* ********** Banner2 Area Start ********* */
#banner2{ background-color: #f2f2f2;}
#banner2 .container{padding-bottom: 84px;}
#banner2 li img {width: 100%;}



/* ********** Footer Area Start ********* */
#footer{background-color: #333;}
#footer .container{padding: 21px 0;}
#footer .container a{
  font-size: 2.4rem; color: #fff; margin: 0 3px;
}
#footer .container a:hover{color: #fc0;}
#footer .container p{
  width: 100%; height: 21px; text-align: right; color: #fff; padding-top: 5px;
}





/* ******************** Media Query Area Start ************************* */

@media screen and (max-width: 1200px){

  /* *****   ***** */

  /* Layout Area Reset */
  .container{padding: 105px 15px;}
  .container-fluid .row{width: 100% !important;}

}

/* ************ Desktop Area Start ************* */
@media screen and (max-width: 991px){

  /* *****   ***** */

  /* Layout Area Reset */
  .container{width: 940px; padding: 105px 10px;}


  /* *****   ***** */
  /* Navi Area Start */
  #navi .container{ padding: 0 10px; }

  /* Intro Area Start */
  #intro #slides .caption{width: 100%;}
  #intro #slides>div:nth-child(1) .caption{padding-left: 30px;}

  /* About Area Start */
  #about .col-md-4{width: 33.3%; padding: 0 10px;}

  /* Work Area Start */
  #work .content>div:first-child>div,
  #work .content>div:last-child>div{height: 462px;}
  #work .content>div:last-child>div:first-child{height: 924px;}

  /* Banner1 Area Start */
  #banner1 .col-md-6{width: 50%; float: left;}
  #banner1 .container-fluid{padding: 0 10px;}
  #banner1 .container-fluid .row h2{
    width: 100%; font: 300 3.8rem/1 'Roboto', sans-serif;
  }
  #banner1 .container-fluid .row>div:last-child{
    padding: 0;
    text-align: right;
  }

  /* Features Area Start */
  #features .col-md-4{width: 33.3%;}
  #features .col-md-4:nth-child(n+4){margin-bottom: 42px;}

  /* Analysis Area Start */
  #analysis .container-fluid{padding: 105px 10px;}
  #analysis .col-md-6{width: 50%; float: left;}

  /* Plan Area Start */
  #plan .col-md-6{margin-bottom: 63px;}
  #plan .col-md-6:last-child{margin-bottom: 0;}

  /* Footer Area Start */
  #footer .container{padding: 21px 10px;}
  #footer .col-md-6{width: 50%; float: left;}

}


/* ************ Desktop & Tablet Area Start ************* */
@media screen and (min-width: 768px) and (max-width: 959px) {
   /* *****   ***** */

  /* Layout Area Reset */
  .container{width: 100% !important;}

  /* Desktop Hidden Area Start */
  #banner1 .container-fluid, 
  #analysis .container-fluid{display: none;}

  /* Mobile Show Area Start */
  #banner1 .container, 
  #analysis .container{display: block;}


  /* *****   ***** */

   /* About Area Start */
   #about .col-md-4{ width: 100%; height: 0 64px; margin-bottom: 63px;}
   #about .col-md-4:last-child{margin-bottom: 0;}
 
 
   /* Work Area Start */
   #work .content>div:first-child>div, #work .content>div:last-child>div{
     height: 368px;
   }
   #work .content>div:last-child>div:first-child{
     height: 736px;
   }


   /* Banner1 Area Start */
  #banner1{
    background: url(../images/sm/banner-bg.jpg) no-repeat top center;
    background-size: cover;
    color: #fff;
    text-align: center;
  }
  #banner1 h2{margin-bottom: 42px;}


  /* Features Area Start */
  #features .container{padding: 105px 64px;}
  #features .col-md-4{width: 100%;}
  
  
  /* Analysis Area Start */
  #analysis{
    background: url(../images/sm/analysis-bg.jpg) no-repeat top center;
    background-size: cover;
  }
  #analysis .container{padding: 105px 64px; color: #fff;}
  #analysis h2{margin-bottom: 30px;}
  #analysis p{margin-bottom: 63px;}


  /* Plan Area Start */
  #plan .container{padding: 105px 44px;}
  #plan .container .col-md-pull-6{padding: 0;}
  
  
  /* Banner2 Area Start */
  #banner2 .slider-wrap { width: 630px !important; margin: auto;}
  #banner2 .slider-wrap li { width: 210px !important; }


  /* Footer Area Start */
  #footer{height: 105px;}
  #footer .col-md-6{width: 100%; font: none; text-align: center;}
  #footer .container p{text-align: center;}
}


