/* ************ Mobile Area Start ************* */
@media screen and (max-width: 500px){

  /* *****   ***** */

  /* Intro Area Start */
  #intro{background-image: url(../images/xs/intro-bg.jpg);}
  #intro .caption h2 {font-size: 4.3rem;}

  
