  /* video Area Start */
  #video{
    background: url(../images/xs/video-bg.jpg) no-repeat top center;
    background-size: cover;
  }
  #video .container-fluid{display: none;}
  #video .container{display: block; text-align: center; color: #fff;}
  #video .container h2 {padding: 0 60px; margin-bottom: 21px;}
  #video .container p {margin-bottom: 42px;}


  /* Banner2 Area Start */
  #banner2 .slider-wrap { width: 210px !important; margin: auto;}
  #banner2 .slider-wrap li { width: 210px !important; }












}